@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
.appliedFilter-container {
  display: flex;
  justify-content: space-between;
  width: 13%;
}
.numericFilter-container {
  align-items: baseline;
  display: flex;
  margin: 1rem 0;
}

.numericFilter-selectors {
  width: 40%;
}

.dropdown-input {
  margin-right: 0.8rem;
}

.form-control {
  text-transform: capitalize;
}

.textFilter-container {
  margin: 1rem 0;
  width: 50%;
}

.text-input {
  margin-left: 1.2rem;
}

.sorter-container {
  align-items: baseline;
  display: flex;
  margin: 1rem 0;

}

.sorter-selectors {
  margin-right: 0.2rem;
  width: 40%;
}

.column-sort {
  margin-right: 0.8rem;
}

.radio-input {
  margin: 0 0.8rem;
}

.filters-container {
  padding: 0.8rem;
  margin: 0.8rem 0;
  border: 0.1rem solid navy;
  border-radius: 5px;
}
.header-container {
  background: linear-gradient(#020024, #000000, #585353);
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  color: #fff;
  font-family: 'Audiowide', cursive;
}

img {
  width: 5%;
}

p {
  font-family: Roboto,Arial,"Helvetica Neue",Helvetica,sans-serif;
  font-size: 1.5rem;
}

* {
  box-sizing: border-box;
}

body {
  font-family: Verdana, sans-serif;
  font-size: 16px;
  font-weight: 650;
  padding: 1.5em;
}

